import { useState, useRef } from "react";
import { RiVideoDownloadFill } from "react-icons/ri";
import { BsYoutube } from "react-icons/bs";

import "./CSS/videos-site.css";

const VideosSite = ({ addedVideos }) => {
  const searchInputRef = useRef();
  const [toggler, setToggler] = useState(false);

  return (
    <>
      <main className="video-site">
        <div className="cover">
          <section className="large-icon">
            <div>
              <RiVideoDownloadFill className="icon" />
            </div>
          </section>
          <section className="search">
            <div
              className="feedback-container"
              style={{ color: "white", textAlign: "center" }}
            >
              <span style={{ visibility: "hidden" }}>.</span>
              {searchInputRef?.current?.value &&
                addedVideos?.length > 0 &&
                addedVideos.filter((elements) => {
                  return elements?.title
                    ?.toLowerCase()
                    ?.includes(searchInputRef?.current?.value?.toLowerCase());
                })?.length <= 0 && (
                  <span>
                    <b>No items found</b>
                  </span>
                )}
            </div>

            <div className="input-container">
              <input
                ref={searchInputRef}
                type={"text"}
                placeholder={"Search"}
                onChange={() => {
                  setToggler(!toggler);
                }}
              ></input>
            </div>
            <button
              onClick={(e) => {
                e.preventDefault();
              }}
            >
              Enter video title to search
            </button>
          </section>

          {addedVideos?.length > 0 && (
            <section className="items-container">
              {addedVideos
                ?.sort((a, b) => {
                  return new Date(b?.datereleased) - new Date(a.datereleased);
                })
                .filter((elements) => {
                  if (searchInputRef?.current?.value) {
                    return elements?.title
                      ?.toLowerCase()
                      ?.includes(searchInputRef?.current?.value?.toLowerCase());
                  }
                  return elements;
                })
                .map((elements) => {
                  return (
                    <a
                      href={elements?.link}
                      target={"_blank"}
                      rel={"noreferrer"}
                      key={elements?._id}
                      className="individual-item"
                    >
                      <div className="iframe-container">
                        <iframe
                          style={{ border: "1px solid white" }}
                          src={
                            new URL(elements?.link)?.search !== ""
                              ? `https://youtube.com/embed/${
                                  new URL(elements?.link)?.search?.split("=")[1]
                                }?autoplay=1&mute=1&controls=0&loop=1`
                              : `https://youtube.com/embed${
                                  new URL(elements?.link)?.pathname
                                }?autoplay=1&mute=1&controls=0&loop=1`
                          }
                          title={elements.title}
                          frameBorder="0"
                          allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                          allowFullScreen
                        ></iframe>
                      </div>
                      <div className="title">
                        Watch{" "}
                        {elements?.title[0]?.toUpperCase() +
                          elements?.title
                            ?.slice(1, elements?.title?.length)
                            ?.toLowerCase()}{" "}
                        on youtube{" "}
                        <span>
                          <BsYoutube size={"20px"} />
                        </span>
                      </div>
                    </a>
                  );
                })}
            </section>
          )}

          {/* {searchResults?.length>0  &&  showVideos && <section className="items-container">
                    <div className="close-button"><button onClick={(e)=>{e.preventDefault(); setSearchResults([])}}>X</button></div>
                    {searchResults?.sort((a,b)=>{ return new Date(b?.datereleased) - new Date(a.datereleased)}).
                    map((elements)=>{ return (
                        <div key={elements?._id} className="individual-item" onClick={(e)=>{e.preventDefault(); }}>
                            <div className="iframe-container">
                                <iframe title={elements.title} ></iframe>
                            </div>
                            <dv className='title'>{elements?.title[0]?.toUpperCase() + elements?.title?.slice(1, elements?.title?.length)?.toLowerCase()}</dv>
                        </div>
                    )})}
                </section>}

                <div>{!showVideos && feedback}</div> */}
        </div>
      </main>
    </>
  );
};

export default VideosSite;
